.archimate-row {
  display: flex;
}

.archimate-view-nav {
  background: #252729;
  flex: 0 0 360px;
  height: 98vh;
  margin-top: 0;
  padding-top: .5rem;
  padding: .5rem 1rem;
  overflow-x: hidden;
  overflow-y:auto;
  scrollbar-gutter:stable both-edges;
}
.nav-control {
  background: #030303;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  position: absolute;
  width: 320px;
  padding: .25em;
  top: 0;
  z-index: 100;
}
.nav-model {
  margin-top:3em;
}

a.codemo { font-size:24px; color:#aeaeae; padding:0 .25em;}
a.codemo:hover {color:white}

.archimate-organization-list {
  list-style-type: none;
  padding-left: 0;
}

.archimate-organization-list li {
  padding-left: 0.5em;
}

.archimate-documentation-well {
  margin: 0 0.5em 1.5em 0.5em;
  padding: 0.5em 1em;
}
.archimate-svg-container {
  height: 100%;
  margin: 0;
  overflow: scroll;
  padding: 0.5em;
  position: relative;
  width: 100%;
}
.archimate-diagram-view {
  background-color: #2e3338;
  background-image: radial-gradient(circle at 50% 100%, #262b2f, #000);
  height: 98vh;
  padding: 0;
  flex: 1 1 auto;
}
.archimate-zoombar {
  position: absolute;
  right: 0;
  top: 0;
}

.archimate-zoombar:hover {
  opacity: 1;
}

/* Reset SVG ArchiMate diagrams */
svg p {
  line-height: normal;
  width: auto;
}
svg table {
  border: 0;
  margin: 0;
  max-width: none;
  width: auto;
}

table .checkbox {
  margin-top: 2px;
  margin-bottom: 2px;
}

table .checkbox input {
  margin-top: 2px;
}
